import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Toolbar from "@material-ui/core/Toolbar"
import ScrollTop from "../components/ScrollTop/ScrollTop"
import Fab from "@material-ui/core/Fab"
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import FluidContainerList from "../components/Lists/FluidContainerList"

// articles data query
import useNews from '../data/useNews'

/**
 * Business page
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function NewsPage({ location, navigate }) {
  const news = useNews()

  return (
    <React.Fragment>
      <SiteHeader title="News collections" location={location} navigate={navigate} />
      {/* target for scrollTop component */}
      <Toolbar id="back-to-top-anchor" />
      <Box pt={8} pr={1} pb={2} pl={1}>
        <FluidContainerList edges={news} location={location} />
      </Box>
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  )
}

export default NewsPage
